@import "../../../../stylesheets/variables/media-queries";
@import "../../../../stylesheets/animations";

.compareSection {
    width: 100%;
    min-height: 100vh;
    opacity: 0;
    animation: fade-in-up ease forwards var(--revealTimeout, 250ms);

    display: flex;
    align-items: center;
    justify-content: center;

    @media #{$intermidiate-max} {
        flex-direction: column;
    }

    &.tableActive {
        @media #{$intermidiate-max} {
            + [class*="closeBtn"] {
                display: none;
            }

            > [class*="laptopShowcase"] {
                display: none;
            }
        }

        @media #{$intermidiate-min} {
            [class*="sliderWrapper"] {
                transform: scale(0.8);

                > button {
                    transform: scale(1.25);
                }
            }
        }

        > [class*="laptopShowcase"] {
            max-height: none;
        }
    }
}
