@import "../../../stylesheets/variables/media-queries";

.laptop {
    .imageContainer {
        position: relative;
        width: var(--laptopSize);
        min-height: 130px;
        margin: 0 auto;

        display: flex;
        justify-content: center;
        align-items: center;

        // Transition group
        .imageEnter {
            opacity: 0;
        }

        .imageEnterActive {
            opacity: 1;
            transition: opacity ease var(--colorTransitionDelay);
        }

        .imageExit {
            opacity: 1;
        }

        .imageExitActive {
            opacity: 1;
            transition: opacity ease var(--colorTransitionDelay);
        }

        img {
            display: block;
            position: absolute;
            top: 0;
            left: 50%;
            transform: translateX(-50%);
        }
    }

    .colorSelector,
    h1,
    p {
        opacity: 0;
        animation: ease forwards var(--revealTimeout) var(--revealTimeout);
        animation-name: reveal-content-y;

        @media #{$intermidiate-max} {
            animation-name: reveal-content-x;
        }
    }

    .colorSelector {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 20px auto;
        animation-delay: 75ms;

        @media #{$intermidiate-max} {
            margin: 15px auto;
        }

        button {
            $button-size: 45px;
            $button-size-mobile: 30px;
            $border-size: 3px;
            $border-size-mobile: 2px;

            width: $button-size;
            min-height: $button-size;
            border-radius: 50%;
            border: $border-size solid #d2d2d2;
            transition: transform ease 250ms;
            padding: 0;

            @media #{$intermidiate-max} {
                width: $button-size-mobile;
                min-height: $button-size-mobile;
                border-width: $border-size-mobile;
            }

            &:active {
                transform: scale(0.9);
            }

            &:focus {
                outline: none;
                box-shadow: 0 0 0 $border-size #d2d2d2 inset;
            }

            &.isCurrent {
                box-shadow: 0 0 0 $border-size #fff inset;
            }

            + button {
                margin-left: 10px;
            }
        }
    }

    h1,
    p {
        text-align: center;
    }

    h1 {
        margin-bottom: 0;
        font-size: 35px;
        animation-delay: 150ms;

        @media #{$laptop} {
            font-size: 30px !important;
        }

        @media #{$intermidiate-max} {
            font-size: 22px !important;
            font-weight: 400;
        }
    }

    p {
        font-size: 24px;
        color: #999;
        animation-delay: 175ms;

        @media #{$intermidiate-max} {
            display: none;
        }
    }
}

@mixin reveal-content($direction) {
    @keyframes reveal-content-#{$direction} {
        from {
            opacity: 0;

            @if ($direction == "x") {
                transform: translateX(calc(var(--revealOffset) / 5));
            } @else {
                transform: translateY(calc(var(--revealOffset) / 5));
            }
        }

        to {
            opacity: 1;
            transform: translate(0, 0);
        }
    }
}

@include reveal-content("x");
@include reveal-content("y");
