@import "../../stylesheets/variables/media-queries";

$panelHeight: 80px;

.wrapper {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    height: $panelHeight;
    z-index: 10;
    background-color: #0078d4;
    color: #fff;
    display: flex;
    align-items: center;
    padding: 0 15px;
    transform: translate3d(0, -$panelHeight, 0);
    animation: reveal-panel ease 200ms;
    animation-fill-mode: forwards;

    .wrapperTitle {
        font-size: 16px;
        font-weight: 600;
        margin: 0 40px 0 0;

        @media #{$laptop},
        #{$mobile-only} {
            margin-right: 30px;
        }
    }

    .wrapperControlGroup {
        max-width: 200px;
        display: flex;
        flex-direction: column;
        justify-content: center;

        + .wrapperControlGroup {
            margin-left: 35px;

            @media #{$laptop},
            #{$mobile-only} {
                margin-left: 25px;
            }
        }

        label {
            font-size: 13px;
            line-height: 1;
            letter-spacing: 0.25px;
            margin-bottom: 8px;
        }

        select {
            width: 100%;
            padding: 5px 20px 5px 5px;
            border: 0;
        }
    }

    button {
        min-height: 45px;
        border: 0;
        color: #fff;
        padding: 5px;
        background-color: transparent;

        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;

        + button:not(.copyButton) {
            margin-left: 20px;
        }

        span {
            font-size: 13px;
            line-height: 1;
        }

        &:hover {
            background-color: rgba(#fff, 0.15);
        }

        &:active {
            transform: scale(0.95, 0.95);
        }

        &.copyButton {
            margin-left: auto;

            i {
                font-size: 1.2em;
            }
        }

        &.closeButton {
            padding-top: 10px;

            i {
                font-size: 0.85em;
            }
        }
    }
}
@keyframes reveal-panel {
    from {
        transform: translate3d(0, -$panelHeight, 0);
    }

    to {
        transform: translate3d(0, 0, 0);
    }
}
