@import "../../../../../stylesheets/variables/media-queries";
@import "../../../../../stylesheets/variables/colors";
@import "../../../../../stylesheets/animations";

.loading {
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    opacity: 0;
    animation: reveal ease forwards 250ms;

    @media #{$mobile-only} {
        padding: 25px;
    }

    h1 {
        text-align: center;
        line-height: 1.25;
        margin: 0;

        @media #{$mobile-only} {
            font-size: 30px;
        }
    }
}

.loadingOption {
    $card-size: 100px;
    $card-icon-size: 50px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 40px auto;

    .imageContainer {
        width: $card-size;
        height: $card-size;
        background-color: #fff;
        box-shadow: 0 5px 15px rgba(#000, 0.15);
        margin: 0 auto;
        transition: background-color ease 250ms, transform ease 250ms;
        opacity: 0;
        animation: img-load ease-in-out forwards var(--loadingDuration);

        display: flex;
        justify-content: center;
        align-items: center;
    }

    .imageContainer {
        width: $card-size;
        height: $card-size;
        background-color: #fff;
        box-shadow: 0 5px 15px rgba(#000, 0.15);
        margin: 0 auto;
        transition: background-color ease 250ms, transform ease 250ms;
        opacity: 0;
        animation: img-load ease-in-out forwards var(--loadingDuration);

        display: flex;
        justify-content: center;
        align-items: center;
    }

    img {
        display: block;
        object-fit: contain;
        max-width: $card-icon-size;
    }

    h3 {
        font-size: 16px;
        font-weight: 600;
        text-align: center;
        letter-spacing: 0.25px;
        color: $medium-gray;
        margin: 15px auto 0;
        opacity: 0;
        white-space: nowrap;
        animation: text-load ease-in-out forwards var(--loadingDuration);

        @media #{$mobile-only} {
            font-size: 16px;
        }
    }
}

.loadingProgress {
    position: relative;
    width: 600px;
    height: 7.5px;
    border-radius: 25px;
    background-color: #ccc;
    overflow: hidden;

    @media #{$mobile-only} {
        width: 90%;
        height: 5px;
    }

    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        background-color: #999;
        transform: scaleX(0);
        transform-origin: left;
        animation: load linear forwards var(--loadingDelay);
    }
}

@keyframes load {
    from {
        transform: scaleX(0);
    }

    to {
        transform: scaleX(1);
    }
}

@keyframes img-load {
    0% {
        opacity: 0;
        transform: translateX(50px);
    }

    30%,
    60% {
        opacity: 1;
        transform: translateX(0);
    }

    100% {
        opacity: 0;
        transform: translateX(-50px);
    }
}

@keyframes text-load {
    0% {
        opacity: 0;
    }

    30%,
    60% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}
