// Fabric MDL icons

@font-face {
    font-family: 'FabricMDL2Icons';
    font-style: normal;
    font-weight: 400;
    src: url('../assets/fonts/fabricmdl2icons-2.68.woff2') format('woff2'),
         url('../assets/fonts/fabricmdl2icons-2.68.woff') format('woff'),
         url('../assets/fonts/fabricmdl2icons-2.68.ttf') format('truetype');
}


// Segoe UI

@font-face {
    font-family: 'Segoe UI';
    font-style: normal;
    font-weight: 100;
    src: local("Segoe UI Light"), url('../assets/fonts/SegoeUI-Light.woff2') format('woff2');
}

@font-face {
    font-family: 'Segoe UI';
    font-style: normal;
    font-weight: 400;
    src: local("Segoe UI"), url('../assets/fonts/SegoeUI.woff2') format('woff2');
}

@font-face {
    font-family: 'Segoe UI';
    font-style: normal;
    font-weight: 600;
    src: local("Segoe UI Semibold"), url('../assets/fonts/SegoeUI-SemiBold.woff2') format('woff2');
}

@font-face {
    font-family: 'Segoe UI';
    font-style: normal;
    font-weight: 700;
    src: local("Segoe UI Bold"), url('../assets/fonts/SegoeUI-Bold.woff2') format('woff2');
}
