@import "../../../../../../stylesheets/variables/colors";
@import "../../../../../../stylesheets/placeholders";
@import "../../../../../../stylesheets/mixins";

.comparisonTable {
    position: relative;
    height: 100%;
    max-width: 750px;
    max-height: 85vh;
    justify-content: center;

    $overlay-height: 175px;

    &::after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        height: 0;
        pointer-events: none;
        transform-origin: bottom;
        transition: opacity ease 250ms;
    }

    &.hasOverlay {
        &::after {
            opacity: 1;
            height: $overlay-height;
            background-image: linear-gradient(transparent, $light-gray);
        }
    }

    &:not(.hasOverlay) {
        &::after {
            opacity: 0;
            transform: scaleY(0);
        }
    }

    &,
    > div {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    > div {
        position: relative;
        justify-content: flex-start;
        overflow-y: auto;
        overflow-x: hidden;
        @extend %invisible-scrollbar;
    }

    .tableBtn {
        text-transform: uppercase;
        border: none;
        color: #777;
        font-weight: 600;
        outline: none;
        padding: 5px;
        background-color: $light-gray;

        &:focus,
        &:hover {
            span {
                background-color: #fff;
            }
        }

        &:active {
            span {
                background-color: #999;
                color: #dadada;
            }
        }

        span {
            @include rounded-btn(#dadada, #777, 45px);
            margin: 0 auto 5px;
            transition: background-color ease 250ms;
        }
    }

    .specsSection {
        position: relative;
        font-size: 15px;

        &::after {
            content: "";
            position: absolute;
            top: 0;
            left: 50%;
            transform: translateX(-50%);
            background-color: #707070;
            width: 2px;
            height: var(--tableHeight);
            z-index: -1;
        }

        .tableRow {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            padding: 15px 0;

            > div {
                text-align: center;
                padding: 0 15px;
            }
        }

        h2,
        p {
            margin: 0;
        }

        h2 {
            font-size: 16px;

            &:first-child {
                text-align: right;
            }
        }

        p {
            font-size: 14px;

            &:first-child {
                text-align: right;
            }
        }

        span {
            text-transform: uppercase;
            font-weight: 600;
            background-color: $light-gray;
            color: #333;
        }
    }
}
