@import "../../../../../../stylesheets/variables/media-queries";
@import "../../../../../../stylesheets/variables/colors";
@import "../../../../../../stylesheets/placeholders";
@import "../../../../../../stylesheets/animations";

.questionBackground {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    z-index: -1;
    opacity: 0;
    animation: reveal ease forwards 500ms 250ms;
}

.question {
    opacity: 0;
    animation: slide ease forwards 500ms;

    @media #{$desktop-only} {
        padding: 0 65px;

        &.question_gateway {
            [class*="optionsWrapper"] {
                $option-width: 300px;

                > button {
                    margin-left: 10px;
                    margin-right: 10px;

                    @media #{$laptop} {
                        min-width: $option-width !important;
                    }

                    [class*="imageContainer"] {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        overflow: hidden;

                        img {
                            width: 100%;
                            max-width: $option-width;
                            min-height: 250px;
                        }
                    }

                    [class*="textAndIconContainer"] {
                        max-width: $option-width;
                    }
                }
            }
        }
    }

    &.question_use,
    &.question_features {
        [class*="optionsWrapper"] {
            flex-wrap: wrap;
            margin-left: auto;
            margin-right: auto;
            padding: 0;
        }
    }

    &.question_use {
        [class*="optionsWrapper"] {
            max-width: 750px;
        }
    }

    &.question_features {
        [class*="optionsWrapper"] {
            max-width: 900px;
        }
    }

    &.question_use,
    &.question_features {
        @media #{$mobile-only} {
            [class*="optionsWrapper"] {
                max-width: 350px;
            }
        }
    }
}

.overlapContainer {
    position: relative;
    @extend %invisible-scrollbar;

    $overlay-height: 1px;

    &::after {
        content: "";
        position: fixed;
        top: calc(100% - #{$overlay-height});
        left: 0;
        right: 0;
        max-width: 500px;
        margin: 0 auto;
        height: 0;
        pointer-events: none;
        transform-origin: bottom;
        transition: opacity ease 250ms;
    }

    &.isOverlapping {
        max-height: var(--overlapContainerHeight);
        overflow-y: auto;
        padding: 0 25px;
    }

    &.overlayVisible {
        &::after {
            opacity: 1;
            height: $overlay-height;
            background-color: #ccc;
        }
    }

    .scrollbar {
        position: fixed;
        bottom: 0;
        width: 6px;
        height: var(--overlapContainerHeight);
        background-color: #ccc;

        &,
        &::after {
            border-radius: 10px;
        }

        &::after {
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
            height: 50%;
            background-color: #fff;
            transform: translateY(var(--containerScrollbarPos, 0));
            z-index: 1;
        }
    }
}

@keyframes slide {
    from {
        opacity: 0;
        transform: translateX(var(--fadeSlideOffset));
    }

    to {
        opacity: 1;
        transform: translateX(0);
    }
}
