@import "../../../../../../stylesheets/variables/media-queries";
@import "../../../../../../stylesheets/variables/colors";
@import "../../../../../../stylesheets/animations";
@import "../../../../../../stylesheets/mixins";

.navButtons {
    position: fixed;
    bottom: 25px;
    left: 50%;
    transform: translateX(-50%);
    overflow: hidden;
    padding-top: 15px;

    display: flex;
    justify-content: center;
    align-items: center;

    @media #{$mobile-only} {
        position: static;
        transform: none;
        bottom: 10px;
        margin: 25px auto 150px;
    }

    button {
        background-color: transparent;
        border: none;
        opacity: 0;
        animation: fade-in-up ease forwards 250ms;

        + button {
            margin-left: 100px;
        }

        &:focus {
            outline: none;

            .buttonIcon {
                background-color: #f9f9f9;
            }
        }

        span {
            display: block;
        }

        .buttonIcon {
            @include rounded-btn(#fff, #999);

            + span {
                font-size: 17px;
                margin-top: 5px;
                color: $medium-gray;
            }
        }
    }
}
