%invisible-scrollbar {
    -ms-overflow-style: none;
    scrollbar-width: none;
    &::-webkit-scrollbar {
        width: 0;
        height: 0;
    }
    &::-webkit-scrollbar-track,
    &::-webkit-scrollbar-thumb {
        background: transparent;
    }
    &::-webkit-scrollbar-thumb {
        border: none;
    }
}

%no-touch-action {
    *,
    *::before,
    *::after {
        touch-action: none;
    }
}
