@import "../../../stylesheets/variables/media-queries";
@import "../../../stylesheets/placeholders";
@import "../../../stylesheets/mixins";

.selectionSlide {
    &,
    .animationWrapper {
        width: 100%;
        min-height: 100vh;
        overflow: hidden;
    }

    .animationWrapper {
        max-height: 100vh;
        overflow-y: var(--animWrapperOverflowY, auto);
        opacity: 0;
        transition: opacity ease var(--initialSelectionDelay),
            transform ease var(--initialSelectionDelay);
        @extend %invisible-scrollbar;

        &.entering {
            transform: translateY(50px);
        }

        &.entered {
            opacity: 1;
        }

        &.exiting {
            transform: translateY(50px);
        }
    }

    .closeBtn {
        @include rounded-btn(#fff, #999);
        border: 2px solid #d2d2d2;
        position: fixed;
        top: 50px;
        right: 25px;

        @media #{$mobile-only} {
            top: 40px;
            right: 15px;
        }
    }

    [class*="Heading"] {
        margin-bottom: 30px;

        @media #{$desktop-only} {
            margin-bottom: 50px;
        }
    }
}
