@import "./variables/_media-queries.scss";

@mixin reveal-animation($delay, $fade: true) {
    opacity: 0;

    @if $fade {
        transform: translateX(var(--transitionDirection, 0));
        animation: side-reveal ease forwards 400ms #{$delay};
    } @else {
        animation: simple-reveal ease forwards 250ms #{$delay};
    }
}

@keyframes side-reveal {
    from {
        opacity: 0;
        transform: translateX(var(--transitionDirection, 0));
    }

    to {
        opacity: 1;
        transform: translateX(0);
    }
}

@keyframes simple-reveal {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@mixin ripple-animation($expansionStart: 0, $expansionEnd: 1.4) {
    @keyframes ripple {
        0% {
            opacity: 0.75;
            transform: scale($expansionStart);
        }

        25% {
            opacity: 0.75;
        }

        100% {
            opacity: 0;
            transform: scale($expansionEnd);
        }
    }
}

@mixin language-custom-styles($languages) {
    @each $language in $languages {
        [data-language="#{$language}"] {
            @content;
        }
    }
}

@mixin fade-in-animation($direction, $value) {
    @keyframes fade-in-#{$direction} {
        from {
            opacity: 0;
            @if $direction == "left" {
                transform: translateX(#{-$value});
            } @else if $direction == "right" {
                transform: translateX(#{$value});
            } @else if $direction == "up" {
                transform: translateY(#{$value});
            } @else if $direction == "down" {
                transform: translateY(#{-$value});
            }
        }
        to {
            transform: translate(0, 0);
            opacity: 1;
        }
    }
}

@mixin rounded-btn($background, $text, $size: 50px, $font-size: 13px) {
    display: block;
    width: $size;
    min-height: $size;
    background-color: $background;
    color: $text;
    font-size: $font-size;
    border-radius: 50%;
    border: none;

    display: flex;
    justify-content: center;
    align-items: center;

    @media #{$tablet} {
        width: $size / 1.5;
        min-height: $size / 1.5;
        font-size: 10px;
    }

    @media #{$height-lg} {
        width: $size / 1.25;
        min-height: $size / 1.25;
        font-size: 10px;
    }
}
