@import "../../../../../../../stylesheets/variables/colors";
@import "../../../../../../../stylesheets/variables/media-queries";
@import "../../../../../../../stylesheets/placeholders";

.optionsWrapper {
    width: 100%;
    scroll-behavior: smooth;
    @extend %invisible-scrollbar;

    display: flex;
    justify-content: center;

    @media #{$desktop-only} {
        align-items: flex-start;
    }

    @media #{$mobile-only} {
        align-items: flex-start;
        flex-wrap: wrap;
        padding: 0 25px;
    }

    &.hasOverflow {
        position: relative;
        justify-content: flex-start;
        min-height: 550px;
        padding: 25px;
        overflow-x: auto;

        &::after {
            content: "";
            display: block;
            min-width: 25px;
            height: 25px;
        }
    }
}

.optionsScrollbar {
    $scrollbar-width: 65%;
    $scrollbar-height: 6px;

    position: relative;
    width: $scrollbar-width;
    height: $scrollbar-height;
    background-color: #ccc;
    margin: 5px auto 0;
    overflow: hidden;

    &,
    &::after {
        border-radius: 10px;
    }

    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        width: 50%;
        background-color: #fff;
        transform: translateX(var(--optionsScrollbarPos, 0));
    }
}
