@import "../../../stylesheets/animations";

.questionBackground {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    object-position: center;
    object-fit: cover;
    z-index: -1;

    &.backgroundPlaceholder,
    &.backgroundActive {
        animation: reveal ease forwards 500ms 250ms;
    }
}
