@import "../../../../../../stylesheets/variables/colors";
@import "../../../../../../stylesheets/placeholders";
@import "../../../../../../stylesheets/mixins";

.comparisonTableMobile {
    $header-top: 25px;
    $header-height: 20vh;
    $sections-padding: 20px;

    max-height: 100%;
    @extend %invisible-scrollbar;

    @media #{$mobile-only} {
        margin-bottom: 50px;
    }

    .tableHeader {
        position: -webkit-sticky;
        position: sticky;
        top: 0;
        width: 100%;
        min-height: $header-height;
        background-image: linear-gradient($light-gray 95%, rgba($light-gray, 0));
        padding: $sections-padding;
        padding-top: 75px;

        display: flex;
        justify-content: center;
        align-items: center;

        .backButton {
            position: absolute;
            top: 30px;
            left: $sections-padding;
            padding: 0;
            border: none;
            font-weight: 600;
            color: #999;
            background-color: transparent;

            display: flex;
            justify-content: center;
            align-items: center;

            i {
                font-size: 13px;
                margin-right: 10px;
            }
        }

        [class*="laptopShowcase"] {
            min-height: auto;
            padding: 0 25px;

            + [class*="laptopShowcase"] {
                margin-left: $sections-padding;
            }

            img {
                max-width: 250px;

                @media #{$tablet} {
                    width: 200px;
                }
            }
        }

        .headerLaptop {
            flex: 1;

            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;

            img {
                display: block;
                width: 100%;
                max-width: 200px;
                margin: 0 auto;
            }

            h2 {
                text-align: center;
                font-size: 15px;
                font-weight: 100;
                margin: 15px auto 0;
            }
        }
    }

    .tableContent {
        padding: 0 $sections-padding $sections-padding;

        .tableRow {
            position: relative;
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            grid-gap: 15px;
            text-align: center;
            z-index: -1;

            + .tableRow {
                margin-top: 15px;
            }

            &::after {
                content: "";
                position: absolute;
                top: 45px;
                left: 50%;
                transform: translateX(-50%);
                width: 1px;
                height: calc(100% - 35px);
                background-color: darken($light-gray, 25%);
            }

            &:last-child {
                margin-top: 40px;
                text-align: left;

                &::after {
                    top: 0;
                    height: 100%;
                }
            }

            h3 {
                grid-column: 1 / -1;
                font-size: 18px;
                font-weight: 800;
                text-transform: uppercase;
                padding: 10px;
            }

            p {
                font-size: 15px;
                padding: 0 15px;
                margin: 0;
                line-height: 1.75;
            }

            [class*="Disclaimer"] {
                padding: 0 10px;
            }
        }
    }
}
