body {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-family: $main-font;
    touch-action: none;
    color: #000;
    background-color: $light-gray;
    -webkit-overflow-scrolling: touch;
    overflow: hidden;
}

h1 {
    font-weight: 100;
    @include rfs(42px);
    line-height: 1.25;
    margin-bottom: 20px;

    @media #{$height-sm} {
        font-size: 30px !important;
    }

    @media #{$break-md} {
        margin-bottom: 25px;
    }

    @media (min-width: 1400px) {
        @include rfs(48px);
    }
}

p {
    @include rfs(16px);

    @media #{$height-sm} {
        font-size: 16px !important;
    }

    @media (min-width: 1400px) {
        @include rfs(18px);
    }
}

img,
video {
    max-width: 100%;
    pointer-events: none;
}

small {
    display: inline-block;
    @include rfs(14px);
    color: #555;

    @media #{$break-xl} {
        font-size: 13px;
    }
}

.centered-message {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;

    h2 {
        font-weight: 600;
        margin: 0;
        color: #333;
    }

    > svg {
        height: 100px;
        width: 100px;
    }
}

.is-hidden {
    visibility: hidden;
}

.sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    border: 0;
}

.disable-default-touch {
    *,
    *::after,
    *::before {
        touch-action: none;
    }
}
