.ms-Icon {
    display: inline-block;
    font-family: "FabricMDL2Icons";
    font-style: normal;
    font-weight: normal;
    speak: none;
    line-height: 1;
}

// Modifier: Place the icon in a circle

.ms-Icon--circle {
    margin: 0 0.5em 0 0;
    position: relative;
    display: inline-block;
    font-size: 1rem;
    width: 1em;
    height: 1em;
    padding: 0;
    text-align: left;
}

// Modifiers: Size classes

.ms-Icon--xs {
    font-size: 10px;
}

.ms-Icon--s {
    font-size: 12px;
}

.ms-Icon--m {
    font-size: 16px;
}

.ms-Icon--l {
    font-size: 20px;
}

// Adding specific icons
// while we need them

.ms-Icon--ChevronLeft::before {
    content: "";
}

.ms-Icon--ChevronRight::before {
    content: "";
}

.ms-Icon--ChevronUp::before {
    content: "";
}

.ms-Icon--ChevronDown::before {
    content: "";
}

.ms-Icon--ChromeClose:before {
    content: "";
}

.ms-Icon--Link::before {
    content: "";
}

.ms-Icon--Hide::before {
    content: "";
}

.ms-Icon--Info::before {
    content: "";
}

.ms-Icon--Add::before {
    content: "";
}

.ms-Icon--CalculatorSubtract::before {
    content: "";
}

.ms-Icon--Refresh::before {
    content: "";
}
