@import "../../../stylesheets/variables/media-queries";

.scrollBox {
    position: relative;
    overflow: hidden;
    padding-left: 15px;

    &.hasScrollbar {
        > div {
            width: 100%;
            box-sizing: content-box;
            max-height: 75px;
            padding-right: 20px;
            overflow-y: auto;
            scroll-behavior: smooth;

            &::before,
            &::after {
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                width: 4px;
                border-radius: 20px;
                pointer-events: none;
            }

            &::before {
                height: 100%;
                background-color: rgba(var(--scrollBarRGB), 0.15);
            }

            &::after {
                height: 50%;
                background-color: rgba(var(--scrollBarRGB), 0.35);
                transform: translateY(var(--scrollBoxPosition, 0));
            }
        }
    }
}
