@import "../../stylesheets/variables/media-queries";
@import "../../stylesheets/variables/colors";

.darkTheme {
    --navAccentColor: #ccc;
    --navBackground: #000;
    --navMain: #878787;
    --navHover: #222;
    --navActive: #f0f0f0;
}

.navWrapper {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: var(--navHeight);
    padding: 0 30px;
    background-color: var(--navBackground);

    display: flex;
    justify-content: space-between;

    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        height: 7px;
        width: var(--statusBarPosition);
        background-color: var(--navAccentColor);
    }
}

.navArrow {
    padding: 10px;
    font-size: 0;
    color: var(--navMain);
    background-color: transparent;
    border: none;
    border-radius: 1px;
    height: 70%;
    margin: auto 0;

    // ms-Icon style
    &:before {
        display: inline-block;
        font-family: "FabricMDL2Icons";
        font-style: normal;
        font-weight: normal;
        speak: none;
        line-height: 1;
        font-size: 22px;
    }

    &:first-child {
        // `ChevronLeftSmall` icon
        &:before {
            content: "";
        }
    }

    &:last-child {
        // `ChevronRightSmall` icon
        &:before {
            content: "";
        }
    }

    &:focus {
        outline: 0;
    }

    &:focus,
    &:hover {
        background-color: var(--navHover);
    }

    &:active {
        transform: scale(0.85);
    }

    &:disabled {
        pointer-events: none;

        &:before {
            opacity: 0.4;
        }
    }
}

.navList {
    list-style: none;
    padding: 0;
    margin: 0 auto;
    display: flex;
    align-items: center;
    overflow-x: auto;
    overflow-y: hidden;
    max-width: 100%;
    -ms-overflow-style: -ms-autohiding-scrollbar;

    li {
        display: flex;
        align-items: center;

        @media #{$break-md} {
            + li {
                margin-left: 20px;
            }
        }

        @media #{$break-lg} {
            + li {
                margin-left: 40px;
            }
        }
    }
}

.navButton {
    background-color: transparent;
    padding: 5px 10px;
    color: var(--navMain);
    border: none;
    font-size: 16px;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    transition: color ease 150ms;
    border-radius: 1px;
    transition: background-color ease 100ms;
    white-space: nowrap;

    @media #{$break-md} and #{$tablet} {
        font-size: 14px;
    }

    &:focus {
        outline: 0;
    }

    &:focus,
    &:hover {
        background-color: var(--navHover);
    }

    &:active {
        transform: scale(0.95);
    }

    .active & {
        font-weight: 600;
        color: var(--navActive);
        pointer-events: none;
    }
}
