@import "../../../../../../stylesheets/variables/media-queries";
@import "../../../../../../stylesheets/animations";

@mixin box_shadow($color) {
    box-shadow: 0 5px 10px rgba($color, 0.1), 0 5px 10px rgba($color, 0.15);
}

.progress {
    position: fixed;
    top: 50%;
    right: 25px;
    transform: translateY(-50%);

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    @media #{$mobile-only} {
        right: 10px;
    }

    &.progress_gateway,
    &.progress_type,
    &.progress_storage,
    &.progress_features {
        .progressDot {
            --progressDotColor: #b7b7b7;

            &.isActive {
                @include box_shadow(#555);
            }
        }
    }

    .progressDot {
        --progressDotColor: #fff;

        $dot-size: 15px;
        $mobile-dot-size: 8px;

        width: $dot-size;
        height: $dot-size;
        border-radius: 50%;
        border: 1px solid var(--progressDotColor);
        opacity: 0;
        animation: fade-in-up ease forwards 250ms;

        @media #{$mobile-only} {
            width: $mobile-dot-size;
            height: $mobile-dot-size;
        }

        &.isActive {
            background-color: var(--progressDotColor);
            @include box_shadow(#444);
        }

        + .progressDot {
            margin-top: 35px;
        }
    }
}
