@import "../../../../../stylesheets/variables/media-queries";

$startOverBtnHeight: 50px;

.results {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    overflow-y: auto;

    @media #{$mobile-only} {
        max-height: 100vh;
        padding: 25px;
    }

    @media #{$desktop-only} {
        min-height: 100vh;
        padding: 75px;
    }

    > h1 {
        font-size: 50px;
        font-weight: 400;
        margin-bottom: 50px;

        @media #{$mobile-only} {
            text-align: center;
            font-size: 30px;
        }
    }

    p {
        margin: 0;
    }
}

.contentWrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 50px;
    align-items: center;

    @media #{$laptop} {
        grid-gap: 25px;
    }

    @media #{$mobile-only} {
        grid-template-columns: 1fr;
        grid-gap: 35px;
    }

    [class^="Laptop"] {
        @media #{$mobile-only} {
            grid-row: 2;
        }

        [class*="laptopImage"] {
            max-width: 30vw;
            margin: 0 auto;

            @media #{$laptop} {
                max-width: 100%;
            }

            @media #{$mobile-only} {
                max-width: 300px;
            }
        }

        [class*="colorSelector"] {
            opacity: 1;
        }

        h1,
        p {
            display: none;
        }
    }
}

.resultName {
    grid-column: 1 / -1;

    h2,
    p {
        @media #{$mobile-only} {
            text-align: center;
        }
    }

    h2 {
        font-weight: 400;
        line-height: 1;
        margin-bottom: 10px;

        @media #{$mobile-only} {
            font-size: 20px;
        }
    }

    p {
        font-weight: 20px;
    }
}

.resultSpecs {
    @media #{$mobile-only} {
        padding-bottom: 50px;
    }

    [class*="disclaimer"] {
        position: relative;
        z-index: 1;

        @media #{$mobile-only} {
            margin-bottom: 75px;
        }
    }
}

.resultDetails {
    padding: 0;
    list-style: none;

    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px 75px;

    @media #{$laptop} {
        grid-template-columns: 1fr;
        grid-gap: 30px;
    }

    h3 {
        font-weight: 600;
        font-size: 16px;
    }
}

.startOverBtn {
    background-color: #fff;
    padding: 15px;
    margin-bottom: 25px;
    border: none;
    transition: background-color ease 250ms;
    margin: 50px 0 75px;

    display: inline-flex;
    justify-content: center;
    align-items: center;

    @media #{$mobile-only} {
        background-color: transparent;
        margin: 25px auto;

        &:hover {
            background-color: #f9f9f9;

            i,
            span {
                color: #444;
            }
        }
    }

    &:focus,
    &:hover {
        background-color: #999;

        i,
        span {
            color: #fff;
        }
    }

    &:focus {
        outline: none;
    }

    i,
    span {
        transition: color ease 250ms;
    }

    i {
        transform: rotate(-90deg) rotateY(180deg);
        margin-right: 7.5px;
        color: #999;
    }

    span {
        text-transform: uppercase;
        font-weight: 700;
        letter-spacing: 0.25px;

        @media #{$mobile-only} {
            font-size: 13px;
            margin-left: 5px;
            white-space: nowrap;
        }
    }
}
