$message-width: 7px;
$message-height: 30px;
$message-color: #666;

.scrollMessage {
    position: absolute;
    bottom: -$message-height / 2;
    left: 50%;
    width: $message-width;
    height: $message-height;
    background-image: linear-gradient(rgba($message-color, 0.35), rgba($message-color, 0.25));
    transform: translateX(-50%);
    border-radius: 50px;
    z-index: 1;

    &::before,
    &::after {
        content: "";
        position: absolute;
        top: 0;
        width: $message-width;
        height: $message-width;
        border-radius: 50%;
    }

    &::before {
        background-color: rgba($message-color, 0.25);
    }

    &::after {
        background-color: rgba($message-color, 0.5);
        animation: scroll-message ease-out infinite 1s;
    }
}

@keyframes scroll-message {
    from {
        transform: translateY($message-height);
        opacity: 1;
    }

    to {
        transform: translateY(2.5px);
        opacity: 0;
    }
}
