@import "../../../../../stylesheets/variables/media-queries";
@import "../../../../../stylesheets/placeholders";
@import "../../../../../stylesheets/mixins";

:root {
    --laptopSize: 450px;

    @media #{$intermidiate-max} {
        --laptopSize: 300px;
    }

    @media #{$tablet} {
        --laptopSize: 250px;
    }

    @media #{$mobile-sm} {
        --laptopSize: 175px;
    }
}

.laptopShowcase {
    justify-content: center;
    padding: 25px;

    &,
    .sliderWrapper {
        width: var(--laptopSize);
        min-height: 85vh;
        display: flex;
        flex-direction: column;
        align-items: center;

        @media #{$intermidiate-max} {
            width: 100%;
            max-width: 75vw;
            min-height: 350px;
        }

        @media #{$mobile-only} {
            max-width: none;
        }

        @media #{$mobile-sm} {
            min-height: 225px;
        }
    }

    .sliderWrapper {
        justify-content: space-between;
        transition: transform ease 500ms;
        @extend %no-touch-action;

        @media #{$intermidiate-max} {
            flex-direction: row;
            padding: 0;
        }

        > button {
            @include rounded-btn(#dadada, #777);
            transition: background-color ease 250ms;
            z-index: 1;

            @media #{$intermidiate-max} {
                margin-bottom: 50px;
            }

            &:hover {
                background-color: #fff;
            }

            &:focus {
                outline: none;
            }
        }

        // Transition group
        .laptopEnter,
        .laptopExit,
        .laptopEnterActive,
        .laptopExitActive,
        .laptopEnterActive,
        .laptopExitActive {
            position: relative;
        }

        .laptopEnter {
            top: var(--revealOffset);
            opacity: 0;
            transform: scale(0.9);

            @media #{$intermidiate-max} {
                top: 0;
                left: var(--revealOffset);
            }
        }

        .laptopExit,
        .laptopEnterActive {
            top: 0;
            left: 0;
            opacity: 1;
            transform: scale(1);
        }

        .laptopExitActive {
            opacity: 0;
            top: var(--revealOffset);
            transform: scale(0.9);

            @media #{$intermidiate-max} {
                top: 0;
                left: var(--revealOffset);
            }
        }

        .laptopEnterActive,
        .laptopExitActive {
            transition: top ease var(--revealTimeout), left ease var(--revealTimeout),
                opacity ease var(--revealTimeout), transform ease var(--revealTimeout);
        }

        > div {
            margin: 5vh auto;

            @media #{$intermidiate-max} {
                margin: 0 auto;
            }
        }
    }

    [class^="Disclaimer"] {
        min-height: 85px;
    }

    .disclaimersToRight {
        text-align: right;

        [class^="ScrollBox"] {
            padding-left: 0;

            > div {
                small {
                    margin-right: 15px;
                }

                &::before,
                &::after {
                    left: auto;
                    right: 0;
                }
            }
        }
    }
}
