.main {
    position: relative;
    user-select: none;
    width: 100vw;
    height: calc(100vh - var(--navHeight));
    overflow: hidden;

    &.navDisabled {
        height: auto;
        min-height: 100vh;
    }

    &:not(.navDisabled) {
        > section {
            position: absolute;
            left: 0;
            top: 0;
            bottom: 0;
            right: 0;
            transform: translate3d(var(--currentPosition), 0, 0);

            display: flex;
            flex-direction: column;
            justify-content: center;
        }
    }
}
