@import "../../../../../stylesheets/variables/media-queries";
@import "../../../../../stylesheets/mixins";

.showDetailsBtn {
    border: none;
    margin: 0 100px;
    background-color: transparent;

    &:focus {
        outline: none;
    }

    .imageContainer {
        $container-size: 75px;
        $container-responsive-size: 55px;

        position: relative;
        @include rounded-btn(#fff, var(--navAccentColor), 30px);
        min-width: $container-size;
        height: $container-size;
        margin: 0 auto;

        @media #{$intermidiate-max} {
            min-width: $container-responsive-size;
            height: $container-responsive-size;
        }

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: transparent;
            border-radius: 50%;
            border: 3px solid #d2d2d2;
            animation: ripple ease infinite 1s;
        }

        img {
            display: block;
            height: 35px;
            object-fit: contain;

            @media #{$intermidiate-max} {
                height: 30px;
            }
        }
    }

    span {
        display: block;
        margin-top: 15px;
        font-size: 18px;
        color: #000;
        white-space: nowrap;

        @media #{$intermidiate-max} {
            margin-top: 7.5px;
        }
    }
}

@keyframes ripple {
    from {
        transform: scale(1);
        opacity: 1;
    }

    to {
        transform: scale(1.5);
        opacity: 0;
    }
}
