@import "../../../stylesheets/variables/media-queries";

.heading {
    text-align: center;
    padding-top: 10vh;

    @media #{$mobile-only} {
        padding-left: 25px;
        padding-right: 25px;
    }

    h1,
    h2 {
        margin: 0;
    }

    h1 {
        font-weight: 300;

        + p {
            margin-top: 10px;
        }

        + h2 {
            margin-top: 10px;
        }
    }

    h2,
    p {
        font-weight: 100;
    }

    h2 {
        + p {
            margin-top: 5px;
        }
    }

    p {
        font-size: 20px;
        margin: 0;

        @media #{$mobile-only} {
            font-size: 16px;
        }
    }
}
