@import "../../../../../../../stylesheets/variables/media-queries";
@import "../../../../../../../stylesheets/variables/colors";
@import "../../../../../../../stylesheets/placeholders";

.slider {
    $slider-color: #fff;

    --sliderDotWidth: 18px;
    --sliderMarkerWidth: 45px;

    position: relative;
    max-width: 50vw;
    height: 4px;
    margin: 100px auto;
    background-color: $slider-color;
    @extend %no-touch-action;

    display: flex;
    align-items: center;
    justify-content: space-between;

    @media (max-aspect-ratio: 3/2) {
        margin-bottom: 85px;
    }

    @media #{$mobile-only} {
        --sliderMarkerWidth: 35px;

        max-width: 80vw;
        margin: 50px auto;
    }

    @media #{$desktop-only} and #{$intermidiate-max} {
        max-width: 70vw;
    }

    .sliderOption,
    .sliderMarker {
        display: block;
        background-color: $slider-color;
        border: none;
        border-radius: 50%;
        padding: 0;
        cursor: pointer;

        &:focus {
            outline: none;
        }
    }

    .sliderOption {
        position: relative;
        width: var(--sliderDotWidth);
        height: var(--sliderDotWidth);

        .sliderOptionText {
            position: absolute;
            top: calc(50% + 30px);
            left: 50%;
            transform: translateX(-50%);
            text-align: center;
            pointer-events: none;
        }

        p,
        span {
            min-width: 150px;

            @media #{$mobile-only} {
                display: none;
            }
        }

        p {
            font-size: 17px;
            line-height: 1.5;
            margin: 0;

            @media #{$mobile-only} {
                font-size: 14px;
            }
        }

        span {
            font-size: 13px;
            line-height: 1;
        }
    }

    .sliderMarker {
        position: absolute;
        left: calc(var(--sliderMarkerWidth) * -1 / 3.14);
        width: var(--sliderMarkerWidth);
        height: var(--sliderMarkerWidth);
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
        transform: translateX(var(--markerPosition));
    }
}

.sliderCurrentText {
    text-align: center;
    margin-bottom: 25px;
    pointer-events: none;

    @media #{$desktop-only} {
        display: none;
    }

    p,
    span {
        white-space: nowrap;
        line-height: 1;
    }

    p {
        font-size: 17px;
        margin: 0;
    }

    span {
        font-size: 13px;
    }
}

.sliderImage {
    pointer-events: none;

    > div {
        position: relative;
        height: 25vh;
        display: flex;
        align-items: center;
        justify-content: center;

        @media (max-aspect-ratio: 3/2) {
            height: 20vh;
        }

        @media #{$mobile-only} {
            margin-bottom: 75px;
        }
    }

    img {
        max-width: 225px;
        position: absolute;
        opacity: var(--opacity);
        transform: scale(var(--scaleX), var(--scaleY));
        transform-origin: bottom;

        @media (max-aspect-ratio: 3/2) {
            max-width: 200px;
        }

        @media #{$mobile-only} {
            max-width: 150px;
        }

        &.current {
            opacity: var(--opacityActive);
        }

        &.next {
            opacity: var(--opacityNext);
        }
    }
}
