@import "../../../stylesheets/animations";

.disclaimer {
    --disclaimerRGB: 85, 85, 85;
    --scrollBarRGB: 85, 85, 85;
    position: relative;
    opacity: 0;
    animation: reveal ease forwards 250ms;

    small {
        display: block;
        font-size: 13px;
        color: rgba(var(--disclaimerRGB), 1);
        white-space: pre-wrap;
        cursor: default;
    }
}
