@import "../../stylesheets/variables/sizes";
@import "../../stylesheets/variables/colors";

.wrapper {
    position: fixed;
    top: 0;
    bottom: $slider-nav-height;
    left: 0;
    right: 0;
    padding: 0 15px;

    display: flex;
    align-items: center;
    justify-content: center;
}

.innerContent {
    padding: 20px;
    width: 100%;
    max-width: 750px;
    background-color: #fff;
    box-shadow: 0 6px 7px -4px rgba(0, 0, 0, 0.07), 0 11px 15px 1px rgba(0, 0, 0, 0.07),
        0 4px 20px 3px rgba(0, 0, 0, 0.07);
    opacity: 0;
    animation: fade-in-up ease forwards 250ms 100ms;

    > * + * {
        margin-top: 30px;
    }
}

.heading {
    display: flex;
    align-items: center;
    justify-content: space-between;

    h2 {
        font-weight: 400;
        font-size: 27px;
        margin: 0;
        color: #000;
    }
}

.closeIcon {
    margin-left: auto;
    border: 0;
    background-color: transparent;
    height: 40px;
    width: 40px;

    i {
        display: block;
    }

    &:active {
        transform: scale(0.95, 0.95);
    }
}

.logDataContainer {
    width: 100%;
    min-height: 60vh;
    max-height: 60vh;
    padding: 20px;
    outline: none;
    font-size: 15px;
    user-select: none;
    background-color: #f4f4f4;
    resize: none;
    cursor: pointer;

    + button {
        min-width: 150px;
        display: block;
        margin-top: 15px;
        border: 1px solid #999;
        padding: 5px 10px;
        margin-left: auto;
        background-color: #eee;
        user-select: none;
        transition: background-color ease 250ms;
        overflow: hidden;

        &:hover {
            background-color: #ccc;
        }

        span {
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 13px;

            i {
                margin-right: 5px;
            }
        }
    }
}

@keyframes fade-in-up {
    from {
        opacity: 0;
        transform: translateY(15px);
    }

    60% {
        opacity: 1;
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}
